	module.exports = {"data":{"post":{"html":"<p>MamaMaus reports from Munich. She's at a 2 star hotel with 5 star World Cup rates this week for business meetings before the World Cup opener on Friday. The hotel was apparently was last redecorated for the previous big sports event in Munich, the 1972 Olympics. Her meeting for Thursday was cancelled, she booked a flight home so she can sleep in her own bed one night before returning for the opening match and the hotel lost in the '70s Friday night. She may have VIP tickets, but her hotel is lost in the twilight zone.</p>","fields":{"title":"From the World Cup Front","date":"08 Jun 2006","url":"https://papascott.de/2006-06-08-from-the-world-cup-front/"}}},"pathContext":{"slug":"/2006-06-08-from-the-world-cup-front/","prev":{"fields":{"title":"Never Talk to Tele2","date":"06 Jun 2006","slug":"/2006-06-06-never-talk-to-tele2/"}},"next":{"fields":{"title":"USA - Czech Republic","date":"12 Jun 2006","slug":"/2006-06-12-usa-czech-republic/"}}}}

